import React, { useRef, useState, cloneElement } from 'react';
import './NewCarousel.css'; // Import CSS file for styling

const NewCarousel = ({ children, duplicationFactor }) => {
    const carouselRef = useRef(null);
    const [scrollPosition, setScrollPosition] = useState(0);

    const handleScroll = () => {
        const scrollOffset = carouselRef.current.scrollLeft;
        const containerWidth = carouselRef.current.offsetWidth;
        const scrollWidth = carouselRef.current.scrollWidth;

        // Check if reached the end of the carousel
        if (scrollOffset + containerWidth === scrollWidth) {
            // Reached the end, reset scroll position to the beginning
            carouselRef.current.scrollTo({ left: 0 });
            setScrollPosition(0);
        } else {
            setScrollPosition(scrollOffset);
        }
    };

    // Duplicate the children based on the duplicationFactor
    const duplicatedChildren = [];
    for (let i = 0; i < duplicationFactor; i++) {
        React.Children.forEach(children, child => {
            duplicatedChildren.push(cloneElement(child, { key: `${child.key}-${i}` }));
        });
    }

    return (
        <div className="carousel" ref={carouselRef} onScroll={handleScroll}>
            {/* Carousel content */}
            <div className="carousel__container">
                {duplicatedChildren}
            </div>
        </div>
    );
}

export default NewCarousel;
