import React, { useState } from "react";
import { Container, Form, FormGroup, FormText, Input, Label } from "reactstrap";
import * as Yup from "yup";
import { useFormik } from "formik";
import { differenceInYears } from "date-fns";
import "./RegistrationView.css";


function handleSubmit(onSignup) {
    return async (values) => {
        // Send the data to the backend
        const response = await fetch("http://localhost:8000/auth/signup", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                email: values.email,
                password: values.password,
                username: values.username,
                dob: values.birthdate,
                profession: values.profession,
                number: values.number,
                gender: values.gender,
            }),
        });
        // Handle the response
        const data = await response.json();
        console.log(data);
        if (response.status === 201 && data.token) {
            // Success
            alert(`Account with id = ${data.id} signed up successfully!`);
            onSignup(data.token, `/users/${data.id}`);
        } else {
            // Error
            alert(`${data.message}`);
        }
    };
}

function SignUp(props) {
    const [showHiddenFields, setShowHiddenFields] = useState(false);

    const responseMessage = (response) => {
        console.log(response);
    };
    const errorMessage = (error) => {
        console.log(error);
    };

    const formik = useFormik({
        initialValues: {
            email: "",
            password: "",
            username: "",
            birthdate: "",
            profession: "",
            number: "",
            gender: "",
        },
        onSubmit: handleSubmit(props.onSignup),
        validationSchema: Yup.object({
            email: Yup.string()
                .email("Please enter a valid email address")
                .matches(
                    /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                    "Invalid email address"
                )
                .required("Required"),
            password: Yup.string()
                .min(8, "Password must be at least 8 characters long")
                .max(35, "Password must be at most 35 characters long")
                .matches(
                    /(?=.*?[A-Z])(?=.*?[#?!@$%^&*-])/,
                    "Password must have at least one uppercase letter and one special character"
                )
                .required("Required"),
            username: Yup.string()
                .matches(/^[a-zA-Z0-9]*$/, "Username can only contain letters and numbers")
                .matches(/^\S*$/, "Username cannot contain white spaces")
                .min(4, "Username must be at least 4 characters long")
                .max(13, "Username must be at most 13 characters long")
                .required("Required"),
            birthdate: Yup.date()
                .required("Required")
                .test(
                    "age",
                    "You must be at least 13 years old to use this platform",
                    function (value) {
                        const currentDate = new Date();
                        const age = differenceInYears(currentDate, new Date(value));
                        return age >= 13;
                    }
                ),
        }),
    });

    const handleFocus = () => {
        setShowHiddenFields(true);
    };

    return (
        <div>
            <Container className={"form-container"}>
                <img
                    src={process.env.PUBLIC_URL + "/logo2.png"}
                    alt="logo"
                    className={"big-logo"}
                />
                <h4>Sign up for an account</h4>
                <Form onSubmit={formik.handleSubmit} className="SignUp">
                    <FormGroup floating>
                        <Input
                            id="username"
                            name="username"
                            placeholder="Username"
                            type="text"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.username}
                        />
                        <Label htmlFor="username">Username</Label>
                        {formik.touched.username && formik.errors.username ? (
                            <div className={"invalid-field"}>{formik.errors.username}</div>
                        ) : null}
                    </FormGroup>{" "}
                    <FormGroup floating>
                        <Input
                            id="email"
                            name="email"
                            placeholder="Email"
                            type="email"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.email}
                        />
                        <Label htmlFor="email">Email</Label>
                        {formik.touched.email && formik.errors.email ? (
                            <div className={"invalid-field"}>{formik.errors.email}</div>
                        ) : null}
                    </FormGroup>
                    <FormGroup floating>
                        <Input
                            id="examplePassword"
                            name="password"
                            placeholder="Password"
                            type="password"
                            onChange={formik.handleChange}
                            onBlur={formik.handleBlur}
                            value={formik.values.password}
                            onFocus={handleFocus}
                        />
                        <Label for="examplePassword">Password</Label>
                        {formik.touched.password && formik.errors.password ? (
                            <div className={"invalid-field"}>{formik.errors.password}</div>
                        ) : null}
                    </FormGroup>{" "}
                    {showHiddenFields === true ? (
                        <div>
                            <FormGroup floating>
                                <Input
                                    id="birthdate"
                                    name="birthdate"
                                    placeholder="Birthdate"
                                    type="date"
                                    onChange={formik.handleChange}
                                    onBlur={formik.handleBlur}
                                    value={formik.values.birthdate}
                                />
                                <Label htmlFor="birthdate">Birthdate</Label>
                                {formik.touched.birthdate && formik.errors.birthdate ? (
                                    <div className={"invalid-field"}>{formik.errors.birthdate}</div>
                                ) : null}
                            </FormGroup>
                        </div>
                    ) : (
                        ""
                    )}
                    <button
                        className={!formik.isValid ? "signup-button" : "signup-button-ready"}
                        type={"submit"}
                        disabled={!formik.isValid}
                    >
                        Sign up
                    </button>
                </Form>

                <div className={"log-in-wrapper"}>
                    <a href={"/https://www.youtube.com/watch?v=xvFZjo5PgG0"}>
                        <button className={"log-in"}>Log in</button>
                    </a>
                </div>

                <p>
                    By signing up, you agree to ffbjeh's{" "}
                    {/*<a href="https://example.com/terms-and-conditions">*/}
                        <a href="https://www.example.com" target="_blank" rel="noopener noreferrer">

                        Terms of Use &amp; Privacy Policy
                    </a>
                </p>
            </Container>
        </div>
    );
}

export default SignUp;
